import * as React from "react";
import {
  JSWindow,
  SplitView,
  TreeView,
  TreeItem,
  TreeItemStyle
} from "@jswf/react";

export function TreeViewTest() {
  const treeViewRef = React.useRef<TreeView>(null);
  const [message, setMessage] = React.useState();
  return (
    <JSWindow width={600} title="TreeView">
      <SplitView>
        <div>
          <button
            onClick={() => {
              const item = treeViewRef.current!.getSelectItem();
              if (item) item.addItem({ label: "" });
            }}
          >
            
          </button>
          <br />
          <br />
          <button
            onClick={() => {
              const item = treeViewRef.current!.getSelectItem();
              if (item) item.remove();
            }}
          >
            
          </button>
          <br />
          <br />
          <button
            onClick={() => {
              const items = treeViewRef.current!.getCheckItems();
              for (const item of items) item.remove();
            }}
          >
            
          </button>
          <br />
          <br />
          {message}
        </div>

        <TreeView
          itemStyle={TreeItemStyle.CHECKBOX}
          ref={treeViewRef}
          onItemClick={item => setMessage(item.getLabel())}
        >
          <TreeItem label="Root">
            <TreeItem label="Data2">
              <TreeItem label="Data3">
                <TreeItem label="Data4"></TreeItem>
              </TreeItem>
            </TreeItem>
            <TreeItem label={<b></b>}>
              <TreeItem
                label={
                  <>
                    
                    <br />
                    
                  </>
                }
              ></TreeItem>
            </TreeItem>
            <TreeItem label={<input defaultValue="TextBox" />} />
          </TreeItem>
        </TreeView>
      </SplitView>
    </JSWindow>
  );
}
