import * as React from "react";
import {
  JSWindow, ListView, SplitView, ListHeaders, ListHeader, ListRow, ListItem, ListViewDragData
} from "@jswf/react";

export function ListViewTest() {
    let listViewRef = React.useRef<ListView>(null);
  const [message, setMessage] = React.useState("");
  let count = 1;
  return (
      <JSWindow width={600} title="ListView" >
        <SplitView>
          <div>
            <button
              onClick={() => {
                //
                listViewRef.current!.addItem([
                  count++,
                  <>
                    <br />
                  </>,
                  0,
                  1
                ]);
              }}
            >
              
            </button>
            <br />
            DOM
            <br />
            <br />
            <button
              onClick={() => {
                const items = listViewRef.current!.getSelectItems();
                for (const item of items)
                  listViewRef.current!.setItem(item, 1, "");
              }}
            >
              
            </button>
            <br />
            
            <br />
            <br />
            <button
              onClick={() => {
                const items = listViewRef.current!.getSelectItems();
                items.sort((a, b) => b - a);
                for (const item of items) listViewRef.current!.removeItem(item);
              }}
            >
              
            </button>
            <br />
            
            <br />
            <br />
            {message}
          </div>
          <ListView
            ref={listViewRef}
            draggable={true}
            onItemClick={(row, col) => {
              //
              const item = listViewRef.current!.getItem(row, col);
              //
              if (item)
                setMessage(`${item.toString()}`);
            }}
            onDrop = {(e)=>{
              const dragData = JSON.parse(e.dataTransfer.getData("text/plain")) as ListViewDragData;
              for(const item of dragData.items)
                listViewRef.current!.addItem( item.labels);
            }}
          >
            <ListHeaders>
              <ListHeader type="number">No</ListHeader>
              <ListHeader width={100}>
                
                <br />
                
              </ListHeader>
              <ListHeader type="number"></ListHeader>
              <ListHeader type="number"></ListHeader>
            </ListHeaders>
            <ListRow>
              <ListItem>{count++}</ListItem>
              <ListItem></ListItem>
              <ListItem>5</ListItem>
              <ListItem>10</ListItem>
            </ListRow>
            <ListRow>
              <ListItem>{count++}</ListItem>
              <ListItem></ListItem>
              <ListItem>10</ListItem>
              <ListItem>40</ListItem>
            </ListRow>
            <ListRow>
              <ListItem>{count++}</ListItem>
              <ListItem></ListItem>
              <ListItem>18</ListItem>
              <ListItem>120</ListItem>
            </ListRow>
            <ListRow>
              <ListItem>{count++}</ListItem>
              <ListItem></ListItem>
              <ListItem>30</ListItem>
              <ListItem>380</ListItem>
            </ListRow>
            <ListRow>
              <ListItem>{count++}</ListItem>
              <ListItem></ListItem>
              <ListItem>40</ListItem>
              <ListItem>700</ListItem>
            </ListRow>
          </ListView>
        </SplitView>
      </JSWindow>
  );
}
